
<style lang="less" scoped>
  #searchBar {
    position: -webkit-sticky;
    position: sticky;
    top: 0rem;
    z-index: 100;
  }
  header {
    width: 100%;
  }
  /* 返回按钮 START */
  /* 返回按钮 END */
  /* 头部个人信息 START */
  .header_user {
    padding-bottom: 20px;
    background: linear-gradient(#07bd8d, #03a099);
  }
  .user_ava {
    position: relative;
  }
  .user_ava>img:nth-child(1) {
    position: absolute;
    top: 20px;
    left: 20px;
    width: 40px;
    height: 60px;
  }
  .user_ava>img:nth-child(2) {
    width: 150px;
    height: 140px;
    border-radius: 50%;
    border: 4px solid #fff;
    margin: 20px 0 0 -20px;
  }
  .user_class {
    display: -webkit-flex;
    display: flex;
  }
  .user_nickname{
    margin: 5px 0;
    &>img{
      width: 40px;
    }
  }
  .user_ava,
  .user_nickname,
  .user_sign {
    text-align: center;
    color: #fff;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 25px;
  }
  .user_sign {
    margin: 0 auto;
    height: 50px;
    line-height: 50px;
    width: 500px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .user_class>div {
    -webkit-box-flex: 1;
    flex: 1;
    text-align: center;
    color: #fff;
    font-size: 25px;
    &>img{
      width: 40px;
      height: 40px;
    }
  }
  /* NAV 导航信息 START */
  nav {
    display: -webkit-flex;
    display: flex;
    height: 70px;
    border-bottom: 20px solid #eee;
    background-color: #fff;
  }
  nav>div {
    -webkit-box-flex: 1;
    flex: 1;
    text-align: center;
    height: 70px;
    line-height: 70px;
    font-size: 25px;
  }
  nav>div {
    position: relative;
  }
  nav>div.active::after {
    position: absolute;
    bottom: 0;
    left: 50%;
    content: '';
    background-color: #000;
    width: 80px;
    height: 4px;
    transform: translateX(-50%);
  }
  /* NAV 导航信息 END */
  /* 头部个人信息 END */
  .attention {
    position: absolute;
    top: 20px;
    right: 60px;
    width: 120px;
    height: 50px;
    background-color: transparent;
    border: 2px solid rgba(255, 255, 255, .5);
    outline: none;
    color: rgba(255, 255, 255, .7);
    cursor: pointer;
  }
  .study_ico {
    margin-left: 5px;
    width: 30px;
    vertical-align: middle;
  }
  .study_time {
    width: 60px;
    height: 60px;
    margin-right: .3125rem;
    vertical-align: middle;
  }
  .account_avater {
    width: 140px;
    height: 140px;
    border-radius: 50%;
    overflow: hidden;
  }
</style>

<template>
  <div>
    <header id="searchBar">
      <div class="back_btn"></div>
      <div class="header_user">
        <div>
          <div class="user_ava">
            <img @click="back" src="../../assets/img/dl_jian_l.png" alt="返回按钮">
            <img class="account_avater" :src="userPer.user.avatar ? userPer.user.avatar : defaultAvatar" alt="用户头像">
            <button class="attention" v-if="userPer.isFollow == 'N' && LOGIN_USER_OBJECT && userPer.user.userId !== LOGIN_USER_OBJECT.user.userId" @click="__attention_add(userPer)">关注</button>
            <button class="attention" v-if="userPer.isFollow == 'Y' && LOGIN_USER_OBJECT && userPer.user.userId !== LOGIN_USER_OBJECT.user.userId" @click="__attention_cancel(userPer)">已关注</button>
          </div>
          <div class="user_nickname">{{userPer.user.nickname}}<img class="study_ico" :src="icon" alt="角色"></div>
          <div class="user_sign" v-if="userPer.user.introduction">{{userPer.user.introduction}}</div>
          <div class="user_class">
            <div><img class="study_time" src="../../assets/img/sjj.png" alt="ico">上次学习{{day.studyTtime}}天前</div>
            <div><img class="study_time" src="../../assets/img/xjj.png" alt="ico">学习时长{{day.studyDay}}天</div>
          </div>
        </div>
      </div>
      <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
        <login class="animated" v-if="!LOGIN_USER_OBJECT && showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
      </transition>
      <nav>
        <router-link tag="div" :to="{ name: 'perWorkCircle', params: {id: this.$route.params.id}}">作品圈</router-link>
        <router-link tag="div" :to="{ name: 'openedE', params: {id: this.$route.params.id}}">开通课程</router-link>
        <router-link tag="div" :to="{ name: 'favoriteWork', params: {id: this.$route.params.id}}">收藏课程</router-link>
        <router-link tag="div" :to="{ name: 'submitedWork', params: { id: this.$route.params.id}}">提交的作业</router-link>
      </nav>
    </header>
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>
<script>
  import login from '@/components/login'
  export default {
    components: {
      login
    },
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    watch: {
      LOGIN_USER_OBJECT(state){}
    },
    data() {
      return {
        day: {},
        userPer: {
          user: {
            avatar: null
          }
        },
        self: null,
        icon: '',
        topicTitle: '',
        showTopic: 0,
        showLoginModal: 0,
        defaultAvatar: require('../../assets/img/eb5974c81833c9fc043a700cfe21aa9d.jpg')
      }
    },
    mounted() {
      this.axios.post('/api/studyTtime', {
        userId: this.$route.params.id
      }).then(data => {
        this.day = data.data.result
      }).catch(err => {
        console.log(err)
      });
      // 查找指定用户信息
      this.axios.get('/api/user', {
        params: {
          id: this.$route.params.id
        }
      }).then(data => {
        this.userPer = data.data
        if (this.userPer.user.avatar.indexOf('http') == -1) {
          this.userPer.user.avatar = this.productURL + this.userPer.user.avatar
        }
        if(this.userPer.user.isVip === 'Y'){
          this.icon = require('../../assets/img/stu.png')
        }else if(this.userPer.user.isTrialCourse === 'Y'){
          this.icon = require('../../assets/img/shi.png')
        }else{
          this.icon = require('../../assets/img/zhu.png')
        }
      }).catch(err => {
        console.log(err)
      });
    },
    methods: {
      showDialogEvent(data) {
        this.showLoginModal = data
      },
      back() {
        this.$router.go(-1)
      },
      __attention_add(item) {
        if(!this.LOGIN_USER_OBJECT){
          this.showLoginModal = 1
          return
        }
        this.axios.post('/api/user-following/add', {
          followingId: item.user.userId
        }).then(data => {

          this.$store.commit('cleanMyFriendAndMyFanceStatus')
          if (data.data.result == '关注成功') {
            this.$set(item, 'isFollow', 'Y')
            this.$Toast({
              message: '关注成功',
              position: 'middle',
              duration: 1000
            })
          } else if (data.data.result == '已添加关注') {
            this.$Toast({
              message: '已添加关注',
              position: 'middle',
              duration: 1000
            })
          } else if (data.data.content == "请登录后进行操作") {
            this.$Toast({
              message: '请登录后进行操作',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err);
        });
      },
      __attention_cancel(item) {
        this.axios.post('/api/user-following/delete', {
          followingId: item.user.userId
        }).then(data => {
          if (data.data.result == '移除成功') {
            this.$store.commit('cleanMyFriendAndMyFanceStatus')
            this.$set(item, 'isFollow', 'N');
            this.$Toast({
              message: '移除成功',
              position: 'middle',
              duration: 1000
            })
          } else if (data.data.result == '移除失败') {
            this.$Toast({
              message: '移除失败',
              position: 'middle',
              duration: 1000
            })
          } else {
            this.$Toast({
              message: '请登录后操作',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        });
      }
    }
  };
</script>



